import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../store';
import { getGatesAsync, updateGatesAsync } from '@slices/gatesSlice';
import { Coordinate, IGate, NullableIGate } from '@typings/db';
import { useParams } from 'react-router';
import { calculateTraveledDistances } from '@utils/gatesUtil';

function useCustomGates() {
  const gatesState = useSelector((state: RootState) => state.gatesSlice);
  const dispatch: AppDispatch = useDispatch();
  const { url } = useParams<{ url: string }>();

  const getGatesAsyncHook = async (url: string): Promise<IGate[]> => {
    const actionPayload = await dispatch(getGatesAsync(url)).unwrap();
    return actionPayload as IGate[];
  };

  function updateGatesWithIndex(index: number, newCoordinate: Coordinate) {
    // 상태의 복사본 생성 및 업데이트
    const updatedGates = [...gatesState];
    updatedGates[index] = {
      ...updatedGates[index],
      coordinate: newCoordinate,
    };
    if (url) {
      const recalculatedGates = calculateTraveledDistances(updatedGates);
      dispatch(updateGatesAsync({ url, gates: recalculatedGates }));
    }
  }

  const updateGatesHook = async (url: string, updateGates: NullableIGate[]): Promise<IGate[]> => {
    const recalculatedGates = calculateTraveledDistances(updateGates);
    const actionPayload = await dispatch(updateGatesAsync({ url, gates: recalculatedGates })).unwrap();
    return actionPayload as IGate[];
  };

  return { gatesState, getGatesAsyncHook, updateGatesWithIndex, updateGatesHook };
}

export default useCustomGates;
